import { DriveDefinition, TargetDrive } from '@youfoundation/dotyoucore-js';
import { CircleDefinition } from '../connections/CircleDataTypes';
import { OwnerProviderBase } from '../OwnerProviderBase';

export interface DriveDefinitionParam extends Omit<DriveDefinition, 'targetDriveInfo'> {
  targetDrive: TargetDrive;
}

//Handles management of the System
export class SystemProvider extends OwnerProviderBase {
  private static instance: SystemProvider;
  static DriveInfo: Promise<Record<string, TargetDrive>>;
  private root = '/config/system';

  constructor(sharedSecret: Uint8Array | undefined) {
    if (!sharedSecret) {
      throw '[SystemProvider] Shared Secret is required';
    }
    super(sharedSecret);
  }

  public async initialize(drives?: DriveDefinitionParam[], circles?: CircleDefinition[]) {
    const client = super.createAxiosClient();
    const url = this.root + '/initialize';
    const data = { drives: drives ?? [], circles: circles ?? [] };

    return client.post<boolean>(url, data).then((response) => {
      return response.data;
    });
  }

  public async isConfigured() {
    const client = super.createAxiosClient();
    const url = this.root + '/isconfigured';
    return client.post<boolean>(url, {}).then((response) => {
      return response.data;
    });
  }

  public static getInstance(sharedSecret): SystemProvider {
    if (!SystemProvider.instance) {
      SystemProvider.instance = new SystemProvider(sharedSecret);
    }

    return SystemProvider.instance;
  }
}
