import { FC, ReactNode } from 'react';
import { useSearchParams } from 'react-router-dom';
import useDarkMode from '../../../hooks/useDarkMode';
import Sidenav from './Sidenav/Sidenav';

interface LayoutProps {
  children?: ReactNode;
}

const Layout: FC<LayoutProps> = ({ children }) => {
  const [searchParams] = useSearchParams();
  const uiSetting = searchParams.get('ui');

  if (uiSetting === 'none') {
    return <NoLayout>{children}</NoLayout>;
  }

  if (uiSetting === 'minimal' || uiSetting === 'focus') {
    return (
      <MinimalLayout>
        <div className="py-4 px-5 sm:py-8 sm:px-10">{children}</div>
      </MinimalLayout>
    );
  }

  return (
    <div className="relative flex flex-row dark:text-gray-400">
      <Sidenav />
      <div className={`flex min-h-screen w-full flex-col dark:bg-gray-900 dark:text-gray-400`}>
        <div className="min-h-full py-4 px-5 sm:py-8 sm:px-10">{children}</div>
      </div>
    </div>
  );
};

export const MinimalLayout: FC<LayoutProps> = ({ children }) => {
  useDarkMode();
  return (
    <div className="relative min-h-screen dark:bg-gray-900 dark:text-gray-400">{children}</div>
  );
};

export const NoLayout: FC<LayoutProps> = ({ children }) => {
  useDarkMode();
  return <div className="dark:bg-gray-900 dark:text-gray-400">{children}</div>;
};

export default Layout;
