import { useMutation } from '@tanstack/react-query';
import {
  ApiType,
  AttributeConfig,
  AttributeDefinitions,
  BlogConfig,
  BuiltInProfiles,
  Client,
  FileQueryParams,
  getChannelDrive,
  getTargetDriveFromProfileId,
  HomePageAttributes,
  HomePageConfig,
  HomePageFields,
  postTypeToTag,
  ProfileConfig,
  SecurityGroupType,
  TargetDrive,
} from '@youfoundation/dotyoucore-js';
import { FileProvider } from '../../provider/file/FileProvider';
import useAuth from '../auth/useAuth';

const useStaticFiles = () => {
  const { getSharedSecret } = useAuth();

  const client = new Client({ api: ApiType.Owner, sharedSecret: getSharedSecret() });

  const publishData = async () => {
    const fileProvider = FileProvider.getInstance(getSharedSecret());

    const publishProfile = async () => {
      const homepageDrive: TargetDrive = {
        alias: HomePageConfig.DefaultDriveId.toString(),
        type: ProfileConfig.ProfileDriveType.toString(),
      };

      const profileDrive: TargetDrive = {
        alias: BuiltInProfiles.StandardProfileId.toString(),
        type: ProfileConfig.ProfileDriveType.toString(),
      };

      const personalInfoNameQueryParam: FileQueryParams = {
        targetDrive: profileDrive,
        fileType: [AttributeConfig.AttributeFileType],
        tagsMatchAll: [
          BuiltInProfiles.PersonalInfoSectionId.toString(),
          AttributeDefinitions.Name.type.toString(),
        ],
      };

      const personalInfoPhotoQueryParam: FileQueryParams = {
        targetDrive: profileDrive,
        fileType: [AttributeConfig.AttributeFileType],
        tagsMatchAll: [
          BuiltInProfiles.PersonalInfoSectionId.toString(),
          AttributeDefinitions.Photo.type.toString(),
        ],
      };

      const SocialQueryParam: FileQueryParams = {
        targetDrive: profileDrive,
        fileType: [AttributeConfig.AttributeFileType],
        tagsMatchAtLeastOne: [
          AttributeDefinitions.TwitterUsername.type,
          AttributeDefinitions.FacebookUsername.type,
          AttributeDefinitions.InstagramUsername.type,
          AttributeDefinitions.TiktokUsername.type,
          AttributeDefinitions.LinkedinUsername.type,
          AttributeDefinitions.DotYouIdentity.type,
        ],
      };

      const themeFileQueryParam: FileQueryParams = {
        targetDrive: homepageDrive,
        fileType: [AttributeConfig.AttributeFileType],
        tagsMatchAtLeastOne: [HomePageAttributes.Theme.toString()],
      };

      const bioFileQueryParam: FileQueryParams = {
        targetDrive: profileDrive,
        fileType: [AttributeConfig.AttributeFileType],
        tagsMatchAtLeastOne: [AttributeDefinitions.FullBio.type.toString()],
      };

      const linkFileQueryParam: FileQueryParams = {
        targetDrive: profileDrive,
        fileType: [AttributeConfig.AttributeFileType],
        tagsMatchAtLeastOne: [AttributeDefinitions.Link.type.toString()],
      };

      const homeFileQueryParam: FileQueryParams = {
        targetDrive: homepageDrive,
        fileType: [AttributeConfig.AttributeFileType],
        tagsMatchAtLeastOne: [HomePageAttributes.HomePage.toString()],
      };

      const baseResultOptions = {
        includeAdditionalThumbnails: false,
        includeJsonContent: true,
        includePayload: false,
        excludePreviewThumbnail: false,
      };

      const sections = [
        {
          name: 'socials',
          queryParams: SocialQueryParam,
          resultOptions: baseResultOptions,
        },
        {
          name: 'name',
          queryParams: personalInfoNameQueryParam,
          resultOptions: baseResultOptions,
        },
        {
          name: 'photo',
          queryParams: personalInfoPhotoQueryParam,
          resultOptions: baseResultOptions,
        },
        {
          name: 'bio',
          queryParams: bioFileQueryParam,
          resultOptions: baseResultOptions,
        },
        {
          name: 'link',
          queryParams: linkFileQueryParam,
          resultOptions: baseResultOptions,
        },
        {
          name: 'theme',
          queryParams: themeFileQueryParam,
          resultOptions: baseResultOptions,
        },
        {
          name: 'home',
          queryParams: homeFileQueryParam,
          resultOptions: baseResultOptions,
        },
      ];

      const publicSections = [
        {
          name: 'name',
          queryParams: personalInfoNameQueryParam,
          resultOptions: baseResultOptions,
        },
        {
          name: 'photo',
          queryParams: personalInfoPhotoQueryParam,
          resultOptions: baseResultOptions,
        },
      ];

      const homeAtrributes = await client.homePageProvider.getAttributes(
        HomePageAttributes.HomePage
      );

      // Image fileId's discovery:
      const homeFileId = homeAtrributes?.[0]?.data[HomePageFields.HeaderImageId];
      if (homeFileId) {
        try {
          const homeTargetDrive = getTargetDriveFromProfileId(
            HomePageConfig.DefaultDriveId.toString()
          );

          const imageFileHeader = await client.driveProvider.GetFileHeader(
            homeTargetDrive,
            homeFileId
          );
          const uniqueId = imageFileHeader.fileMetadata.appData.uniqueId;

          const headerImageQueryParam: FileQueryParams = {
            targetDrive: homeTargetDrive,
            fileType: [0],
          };

          if (uniqueId) {
            headerImageQueryParam.clientUniqueIdAtLeastOne = [uniqueId];
          }

          sections.push({
            name: homeFileId,
            queryParams: headerImageQueryParam,
            resultOptions: baseResultOptions,
          });
        } catch (ex) {
          console.log('No header images found => none published');
        }
      }

      const profileAttributes = await client.profileDataProvider.getAttributeVersions(
        BuiltInProfiles.StandardProfileId,
        BuiltInProfiles.PersonalInfoSectionId,
        [AttributeDefinitions.Photo.type]
      );

      const profilePhotoFileIds = profileAttributes
        ?.filter(
          (attr) =>
            attr.acl.requiredSecurityGroup.toLowerCase() ===
            SecurityGroupType.Anonymous.toLowerCase()
        )
        ?.map((attr) => attr?.data?.['profileImageId'] as string)
        .filter((fileId) => fileId !== undefined);

      if (profilePhotoFileIds?.length) {
        try {
          // We only use the first public profile photo id
          const profilePhotoFileId = profilePhotoFileIds[0];

          const profileTargetDrive = getTargetDriveFromProfileId(
            BuiltInProfiles.StandardProfileId.toString()
          );

          const imageFileHeader = await client.driveProvider.GetFileHeader(
            profileTargetDrive,
            profilePhotoFileId
          );
          const uniqueId = imageFileHeader.fileMetadata.appData.uniqueId;

          const profilePhotoQueryParams: FileQueryParams = {
            targetDrive: profileTargetDrive,
            fileType: [0],
          };

          if (uniqueId) {
            profilePhotoQueryParams.clientUniqueIdAtLeastOne = [uniqueId];
          }

          // Only add first anonymous picture into the siteData;

          sections.push({
            name: profilePhotoFileId,
            queryParams: profilePhotoQueryParams,
            resultOptions: baseResultOptions,
          });

          publicSections.push({
            name: profilePhotoFileId,
            queryParams: profilePhotoQueryParams,
            resultOptions: {
              ...baseResultOptions,
              includeAdditionalThumbnails: true,
            },
          });
        } catch (ex) {
          console.log('No profile photos found => none published');
        }
      }

      return await Promise.all([
        await fileProvider.publishFile('sitedata.json', sections),
        await fileProvider.publishFile('public.json', publicSections, 'allowAllOrigins'),
      ]);
    };

    const publishBlog = async () => {
      const baseResultOptions = {
        includeAdditionalThumbnails: false,
        includeJsonContent: true,
        includePayload: false,
        excludePreviewThumbnail: false,
      };
      const channels = await client.blogDefinitionProvider.getChannelDefinitions();
      const sections = channels.map((channel) => {
        const channelDrive = getChannelDrive(channel.channelId);
        const blogOnChannelQuery: FileQueryParams = {
          targetDrive: channelDrive,
          fileType: [BlogConfig.PostFileType, BlogConfig.ChannelDefinitionFileType],
        };
        return {
          name: channel.channelId,
          queryParams: blogOnChannelQuery,
          resultOptions: baseResultOptions,
        };
      });

      const imageSections = (
        await Promise.all(
          (
            await client.blogPostReadonlyProvider.getRecentPosts(
              [postTypeToTag('Article')],
              channels.length * 6
            )
          ).results
            .filter(
              (blog) =>
                blog.acl.requiredSecurityGroup.toLowerCase() !==
                SecurityGroupType.Owner.toLowerCase()
            )
            .map(async (blog) => {
              try {
                const targetDrive = client.blogDefinitionProvider.getTargetDrive(
                  blog.content.channelId
                );

                const imageFileHeader = await client.driveProvider.GetFileHeader(
                  targetDrive,
                  blog.content.primaryImageFileId
                );
                const uniqueId = imageFileHeader.fileMetadata.appData.uniqueId;

                const params: FileQueryParams = {
                  targetDrive: targetDrive,
                  fileType: [0],
                };

                if (uniqueId) {
                  params.clientUniqueIdAtLeastOne = [uniqueId];
                }

                return {
                  name: blog.content.primaryImageFileId,
                  queryParams: params,
                  resultOptions: baseResultOptions,
                };
              } catch (ex) {
                return undefined;
              }
            })
        )
      ).filter((sectionItem) => sectionItem && sectionItem.name?.length !== 0);

      if (!sections.length) {
        return;
      }
      return await fileProvider.publishFile('blogs.json', [...sections, ...imageSections]);
    };

    await Promise.all([await publishProfile(), await publishBlog()]);
  };

  return {
    publish: useMutation(publishData, {
      onError: (ex) => {
        console.error(ex);
      },
    }),
  };
};

export default useStaticFiles;
