import { useMutation } from '@tanstack/react-query';
import { BlogConfig } from '@youfoundation/dotyoucore-js';
import { HomePageConfig } from '@youfoundation/dotyoucore-js';
import {
  ApiType,
  AttributeDefinitions,
  AttributeFile,
  BuiltInProfiles,
  Client,
  DataUtil,
  getTargetDriveFromProfileId,
  LinkFields,
  LocationFields,
  MinimalProfileFields,
  SecurityGroupType,
  SocialProfileFields,
} from '@youfoundation/dotyoucore-js';
import { WelcomeData } from '../../templates/Initialization/Initialization';
import { CircleDefinition } from '../../provider/connections/CircleDataTypes';
import { DriveDefinitionParam, SystemProvider } from '../../provider/system/SystemProvider';
import useAuth from '../auth/useAuth';
import useAttribute from '../profiles/useAttribute';

const anonymousAcl = { requiredSecurityGroup: SecurityGroupType.Anonymous };

const useInit = () => {
  const { getSharedSecret, isAuthenticated } = useAuth();
  const { mutateAsync: saveAttr } = useAttribute({}).save;

  const initDrives: DriveDefinitionParam[] = [
    {
      targetDrive: HomePageConfig.HomepageTargetDrive,
      name: 'Homepage Config',
      metadata: '',
      allowAnonymousReads: true,
      ownerOnly: false,
    },
    {
      targetDrive: BlogConfig.PublicChannelDrive,
      name: 'Public Posts',
      metadata: '',
      allowAnonymousReads: true,
      ownerOnly: false,
    },
  ];

  const doCleanInit = async (isEmptyInit: boolean) => {
    if (!isAuthenticated) {
      return;
    }

    // Initialize
    const systemProvider = SystemProvider.getInstance(getSharedSecret());
    await systemProvider.initialize(initDrives);

    if (!isEmptyInit) {
      const client = new Client({ api: ApiType.Owner, sharedSecret: getSharedSecret() });

      await client.profileDefinitionProvider.ensureConfiguration();
      await client.profileDataProvider.ensureConfiguration();
      await client.blogDefinitionProvider.ensureConfiguration();
    }
  };

  const doInitWithData = async (data: WelcomeData) => {
    if (!isAuthenticated) {
      return;
    }

    const client = new Client({ api: ApiType.Owner, sharedSecret: getSharedSecret() });
    const systemProvider = SystemProvider.getInstance(getSharedSecret());

    const initCircles: CircleDefinition[] = data?.circles?.map((circle) => {
      return {
        id: DataUtil.toGuidId(circle.name),
        name: circle.name,
        description: circle.description,
        permissions: {
          keys: [10],
        },
      };
    });

    // Initialize
    await systemProvider.initialize(initDrives, initCircles);

    // Ensure Config
    await client.profileDefinitionProvider.ensureConfiguration();

    // Skip of Profile Data Provider, as more relevant base data will be created by what follows
    // await client.profileDataProvider.ensureConfiguration();
    await client.blogDefinitionProvider.ensureConfiguration();

    // Save data from welcome wizard:
    // Profile Data:
    const defaultPhotoAttrId = DataUtil.toGuidId('default_photo_attribute');
    const existingPhotoAttr = await client.profileDataProvider.getAttribute(
      BuiltInProfiles.StandardProfileId,
      defaultPhotoAttrId
    );

    if (!existingPhotoAttr) {
      const mediaFileId = (
        await client.mediaProvider.uploadImage(
          getTargetDriveFromProfileId(BuiltInProfiles.StandardProfileId),
          undefined,
          anonymousAcl,
          data.profile.imageData?.bytes,
          undefined,
          data.profile.imageData?.type
        )
      ).fileId;

      const newPhotoAttr: AttributeFile = {
        id: defaultPhotoAttrId,
        profileId: BuiltInProfiles.StandardProfileId,
        type: AttributeDefinitions.Photo.type,
        priority: 1000,
        sectionId: BuiltInProfiles.PersonalInfoSectionId,
        data: {},
        acl: anonymousAcl,
      };

      newPhotoAttr.data[MinimalProfileFields.ProfileImageId] = mediaFileId?.toString();

      await saveAttr(newPhotoAttr);
    }

    const defaultNameAttrId = DataUtil.toGuidId('default_name_attribute');
    const existingNameAttr = await client.profileDataProvider.getAttribute(
      BuiltInProfiles.StandardProfileId,
      defaultNameAttrId
    );

    if (!existingNameAttr) {
      const newNameAttr: AttributeFile = {
        id: defaultNameAttrId,
        profileId: BuiltInProfiles.StandardProfileId,
        type: AttributeDefinitions.Name.type,
        priority: 1000,
        sectionId: BuiltInProfiles.PersonalInfoSectionId,
        data: {},
        acl: anonymousAcl,
      };

      newNameAttr.data[MinimalProfileFields.GivenNameId] = data.profile.givenName;
      newNameAttr.data[MinimalProfileFields.SurnameId] = data.profile.surname;

      await saveAttr(newNameAttr);
    }

    if (data.profile.city || data.profile.country) {
      const defaultLocationAttrId = DataUtil.toGuidId('default_location_attribute');
      const existingLocationAttr = await client.profileDataProvider.getAttribute(
        BuiltInProfiles.StandardProfileId,
        defaultLocationAttrId
      );

      if (!existingLocationAttr) {
        const newLocationAttr: AttributeFile = {
          id: defaultLocationAttrId,
          profileId: BuiltInProfiles.StandardProfileId,
          type: AttributeDefinitions.Location.type,
          priority: 1000,
          sectionId: BuiltInProfiles.PersonalInfoSectionId,
          data: {},
          acl: anonymousAcl,
        };

        newLocationAttr.data[LocationFields.City] = data.profile.city ?? '';
        newLocationAttr.data[LocationFields.Country] = data.profile.country ?? '';

        await saveAttr(newLocationAttr);
      }
    }

    // Social
    const saveSocial = async (type: string, dataField: string, value: string, priority: number) => {
      // Search attribute:
      const foundAttributesOfType = await client.profileDataProvider.getAttributeVersions(
        BuiltInProfiles.StandardProfileId,
        undefined,
        [type]
      );

      if (!foundAttributesOfType?.length) {
        // Create attribute:
        const socialAttribute: AttributeFile = {
          id: DataUtil.getNewId(),
          profileId: BuiltInProfiles.StandardProfileId,
          type: type,
          priority: priority,
          sectionId: BuiltInProfiles.ExternalLinksSectionId.toString(),
          data: {},
          acl: anonymousAcl,
        };

        socialAttribute.data[dataField] = value;

        await saveAttr(socialAttribute);
      }

      return true;
    };

    if (data.social.facebook) {
      await saveSocial(
        AttributeDefinitions.FacebookUsername.type,
        SocialProfileFields.Facebook,
        data.social.facebook,
        1000
      );
    }

    if (data.social.instagram) {
      await saveSocial(
        AttributeDefinitions.InstagramUsername.type,
        SocialProfileFields.Instagram,
        data.social.instagram,
        2000
      );
    }

    if (data.social.tiktok) {
      await saveSocial(
        AttributeDefinitions.TiktokUsername.type,
        SocialProfileFields.Tiktok,
        data.social.tiktok,
        4000
      );
    }

    if (data.social.twitter) {
      await saveSocial(
        AttributeDefinitions.TiktokUsername.type,
        SocialProfileFields.Tiktok,
        data.social.tiktok,
        5000
      );
    }

    if (data.social.linkedin) {
      await saveSocial(
        AttributeDefinitions.LinkedinUsername.type,
        SocialProfileFields.LinkedIn,
        data.social.linkedin,
        6000
      );
    }

    if (data.social.other) {
      await Promise.all(
        data.social.other.map(async (link, index) => {
          const linkAttribute: AttributeFile = {
            id: DataUtil.getNewId(),
            profileId: BuiltInProfiles.StandardProfileId,
            type: AttributeDefinitions.Link.type,
            priority: 10000 + index * 1000,
            sectionId: BuiltInProfiles.ExternalLinksSectionId.toString(),
            data: {},
            acl: anonymousAcl,
          };

          linkAttribute.data[LinkFields.LinkText] = link.text;
          linkAttribute.data[LinkFields.LinkTarget] = link.target;

          return await saveAttr(linkAttribute);
        })
      );
    }
  };

  return {
    init: useMutation(doCleanInit, {
      onError: (ex) => {
        console.error(ex);
      },
    }),
    initWithData: useMutation(doInitWithData, {
      onError: (ex) => {
        console.error(ex);
      },
    }),
  };
};

export default useInit;
