import { useQuery } from '@tanstack/react-query';
import { PagingOptions } from '@youfoundation/dotyoucore-js';
import { CircleNetworkRequestProvider } from '../../provider/connections/CircleNetworkRequestProvider';
import useAuth from '../auth/useAuth';

interface useConnectionsProps {
  pending: { pageSize: number; pageNumber: number };
  sent: { pageSize: number; pageNumber: number };
}

const useConnections = (
  {
    pending: { pageSize: pendingPageSize, pageNumber: pendingPage },
    sent: { pageSize: sentPageSize, pageNumber: sentPage },
  }: useConnectionsProps = {
    pending: { pageSize: 10, pageNumber: 1 },
    sent: { pageSize: 10, pageNumber: 1 },
  }
) => {
  const { getSharedSecret } = useAuth();
  const circleNetworkRequestProvider = CircleNetworkRequestProvider.getInstance(getSharedSecret());

  const fetchPendingConnections = async (
    { pageSize, pageNumber }: PagingOptions = { pageSize: 10, pageNumber: 1 }
  ) => {
    return await await circleNetworkRequestProvider.getPendingRequests({
      pageNumber: pageNumber,
      pageSize: pageSize,
    });
  };

  const fetchSentRequests = async (
    { pageSize, pageNumber }: PagingOptions = { pageSize: 10, pageNumber: 1 }
  ) => {
    return await await circleNetworkRequestProvider.getSentRequests({
      pageNumber: pageNumber,
      pageSize: pageSize,
    });
  };

  return {
    fetchPending: useQuery(
      ['pendingConnections', pendingPageSize, pendingPage],
      () => fetchPendingConnections({ pageSize: pendingPageSize, pageNumber: pendingPage }),
      {
        refetchOnWindowFocus: false,
        keepPreviousData: true,
        onError: (err) => console.error(err),
      }
    ),
    fetchSent: useQuery(
      ['sentRequests', sentPageSize, sentPage],
      () => fetchSentRequests({ pageSize: sentPageSize, pageNumber: sentPage }),
      {
        refetchOnWindowFocus: false,
        keepPreviousData: true,
        onError: (err) => console.error(err),
      }
    ),
  };
};

export default useConnections;
