import { useQuery } from '@tanstack/react-query';
import { SystemProvider } from '../../provider/system/SystemProvider';
import useAuth from '../auth/useAuth';

const useIsConfigured = () => {
  const { getSharedSecret, isAuthenticated } = useAuth();

  const isConfigured = async () => {
    if (!isAuthenticated) {
      return;
    }

    const sharedSecret = getSharedSecret();
    if (!sharedSecret) {
      return;
    }
    const systemProvider = SystemProvider.getInstance(sharedSecret);
    const isConfigured = await systemProvider.isConfigured();

    // if (isConfigured) {
    //   // Even though the system is provided, the init process might have error'd out on the ensure configuration in the providers;
    //   // So -> Ensure configuration on providers:
    //   const client = new Client({ api: ApiType.Owner, sharedSecret: getSharedSecret() });

    //   await client.profileDefinitionProvider.ensureConfiguration();
    //   await client.profileDataProvider.ensureConfiguration();
    //   await client.blogDefinitionProvider.ensureConfiguration();
    // }

    return isConfigured;
  };

  return {
    isConfigured: useQuery(['initialized'], isConfigured, {
      refetchOnMount: false,
      refetchOnWindowFocus: false,
      refetchOnReconnect: false,
    }),
  };
};

export default useIsConfigured;
